<style scoped lang="less">
@import "~@/styles/mixin.less";
@import "~@/styles/var.less";

@itemheight: 30px;
.contact-container {
	margin: 0;
	padding: 0 20px;
	li {
		position: relative;
		margin: 14px 0;
		height: @itemheight;
		list-style: none;
		color: @gray;
		cursor: pointer;
		a {
			line-height: @itemheight;
			font-size: 14px;
			.icon-container {
				margin-right: 10px;
				font-size: 26px;
				vertical-align: middle;
			}
		}
		.ercode {
			position: absolute;
			padding: 10px 15px;
			background-color: white;
			border-radius: 5px;
			bottom: @itemheight+5px;
			left: 0;
			transform-origin: center bottom;
			transform: scaleY(0);
			transition: all 0.3s ease-in-out;
			img {
				width: 150px;
				height: 150px;
			}
			&::after {
				content: "";
				display: block;
				width: 8px;
				height: 8px;
