// 公共的远程获取数据的代码
// 具体的组件中，需要提供一个远程获取数据的方法  fetchData
export default function (defaultDatas = null) {
	return {
		data() {
			return {
				isLoading: true, //是否正在加载
				datas: defaultDatas,
			};
		},
		async created() {
			this.datas = await this.fetchData();
			this.isLoading = false;
		},
	};
}
