<template>
	<div class="about-container" v-loading="loading || urlLoading">
		<iframe
			class="about-iframe"
			:src="url"
			frameborder="0"
			@load="urlLoading = false"
		></iframe>
	</div>
</template>

<script>
import { mapState } from "vuex";
export default {
	data() {
		return { urlLoading: true }; //当前链接地址是否在加载
	},
	computed: mapState("about", ["url", "loading"]),
	created() {
		this.$store.dispatch("about/fetchGetAbout");
	},
};
</script>

<style scoped lang="less">
.about-container {
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
}
.about-iframe {
	width: 100%;
	height: 100%;
	display: block;
	box-sizing: border-box;
}
</style>
